.circle-preloader__conteiner {
	margin: 0;
	background-color: #f5f6f7;
	position: relative;
    box-sizing: border-box;
    padding: 80px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.circle-preloader {
	display: block;
	width: 50px;
	height: 50px;
	border: 4px solid #444;
	border-bottom-color: #888;
	border-radius: 50%;
	margin: auto;
	animation: spin .75s infinite linear;
}

.circle-preloader__text {
font-family: 'Roboto', Arial, sans-serif;
font-style: normal;
font-weight: normal;
font-size: 18px;
line-height: 24px;
text-align: center;
color: #B6BCBF;
}

@keyframes spin {
	100% {
		transform: rotate(360deg);
	}
}